<template>
  <div class="register-box">
    <div class="register-fields">
      <h2 class="m-0 animate-slower">{{ appName }}</h2>
      <h3 class="animate-slower">{{ type }}</h3>
      <form @submit.prevent="Register">
        <div class="form-floating animate-slower" style="color: gray">
          <input
            type="email"
            class="form-control"
            id="emailInput"
            placeholder="name@example.com"
            v-model="email"
            required
          />
          <label for="emailInput">Email address</label>
        </div>
        <div class="form-floating mt-3 animate-slower" style="color: gray">
          <input
            type="password"
            class="form-control"
            id="passInput"
            placeholder="name@example.com"
            v-model="password"
            required
          />
          <label for="passInput">Password</label>
        </div>
        <div class="register-buttons animate-slower">
          <input type="submit" value="Register" class="btn btn-outline-main" />
        </div>
      </form>
      <div class="reg-log-text animate-slower" @click="Login">
        Already registered? <br />
        <span class="reg-log-link">Click here</span>
      </div>
    </div>
  </div>
</template>

<script>
import store from "@/store/index.js";
import { ref } from "vue";
import { auth } from "@/firebase/index.js";
import { createUserWithEmailAndPassword } from "firebase/auth";
import { useRouter } from "vue-router";

export default {
  name: "RegisterComponent",
  props: {
    appName: String,
    type: String,
  },
  setup() {
    const email = ref("");
    const password = ref("");
    const router = useRouter();

    //Prepare Register function
    const Register = async () => {
      try {
        await createUserWithEmailAndPassword(auth, email.value, password.value);
        alert("Account created. Now you can log in.");
      } catch (e) {
        if (
          e == "FirebaseError: Firebase: Error (auth/email-already-in-use)."
        ) {
          alert("Account exists.");
        } else {
          alert("Error occured. Try again in a few seconds");
        }

        email.value = "";
        password.value = "";
      }
    };

    //Prepare redirection to Login form
    const Login = () => {
      router.replace("/");
    };

    return {
      store,
      email,
      password,
      Login,
      Register,
    };
  },
};
</script>

<style scoped>
.register-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
}

.register-fields {
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  background: var(--light-gray);
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1.5rem;
  box-shadow: var(--box-shadow);
}

.register-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  margin-top: 1.5rem;
}

.reg-log-text {
  justify-content: center;
  color: var(--dark-violet);
  font-weight: bolder;
}

.reg-log-link:hover {
  cursor: pointer;
  text-decoration: underline;
}
</style>