<template>
  <div class="content-header">
    <h2>My devices</h2>
  </div>

  <div v-if="!showPage" class="loadingCircle"><LoadingCircle /></div>

  <div v-else class="split-dev-content">
    <div class="split-left">
      <h4 class="split-content-header">Found devices</h4>
      <div v-if="foundDevices == ''" class="animate-slow">No devices found</div>
      <div v-else class="dev-list">
        <div
          v-for="(item, index) in this.foundDevices"
          :key="item"
          :id="'dev_' + index"
          class="dev-item animate-slow"
          @click="selectDevice(index, true)"
          v-bind:class="{
            'dev-warning': !isTimeOkay(item.lastPing),
          }"
        >
          <div class="dev-item-detail">
            <p class="dev-name">{{ item.name }}</p>
            <p>Network: {{ item.ssid }}</p>
          </div>
          <div class="dev-item-warning">
            <img
              src="../assets/exclamation-lg.svg"
              alt="!"
              v-if="!isTimeOkay(item.lastPing)"
            />
          </div>
        </div>
      </div>
    </div>

    <div class="split-right">
      <h4 class="split-content-header">Details</h4>
      <div v-if="!showDetails" class="dev-details-empty animate-slow">
        <p>Select device to show details</p>
      </div>
      <div v-else class="split-dev-details">
        <div class="left">
          <div class="dev-details-filled animate-slow">
            <p>
              <span class="details-field">Device Name: </span>
              {{ foundDevices[selectedDevice].name }}
            </p>
            <p>
              <span class="details-field">Description: </span
              >{{ foundDevices[selectedDevice].description }}
            </p>
            <p>
              <span class="details-field">Network: </span>
              {{ foundDevices[selectedDevice].ssid }}
            </p>
            <p v-if="foundDevices[selectedDevice].sendSms != null">
              <span class="details-field"> SMS notification: </span>
              <span
                v-if="foundDevices[selectedDevice].sendSms"
                style="color: green"
                >&#10004;</span
              >
              <span v-else style="color: red">&#10006;</span>
            </p>
            <p v-if="foundDevices[selectedDevice].sendSms == true">
              <span class="details-field">Phone number: </span
              >{{ foundDevices[selectedDevice].sendSmsNum }}
            </p>
            <p>
              <span class="details-field">Last ping: </span>
              <span v-if="isTimeOkay(foundDevices[selectedDevice].lastPing)">{{
                foundDevices[selectedDevice].lastPing
              }}</span>
              <span v-else class="time-not-ok">{{
                foundDevices[selectedDevice].lastPing
              }}</span>
            </p>
          </div>
        </div>

        <div class="right">
          <div class="del-dev" @click="delDev">Delete device</div>
        </div>
      </div>

      <div v-show="showDetails" class="dev-chart">
        <LineChart @interface="getLinearChartInterface"></LineChart>
      </div>
    </div>
  </div>
</template>

<script>
import { auth, rdb } from "@/firebase/index.js";
import {
  ref,
  query,
  onValue,
  limitToLast,
  remove,
  orderByChild,
  off,
} from "firebase/database";
import { useRouter } from "vue-router";
import LoadingCircle from "@/components/LoadingCircle.vue";
import LineChart from "@/components/LineChartComponent.vue";
import {
  addZeroInTime,
  formatNum,
  numToTimeString,
  stringToNum,
} from "@/utils/index.js";

export default {
  name: "DeviceComponent",
  LinearChartInterface: {
    clearData: () => {},
    sendDevPing: () => {},
  },
  data() {
    return {
      foundDevices: [],
      deviceDetails: [],
      label: [],
      values: [],
      activeListeners: [],
      snaphotExists: false,
      showPage: false,
      showDetails: false,
      selectedDevice: null,
      timer: null,
      onValueListener: null,
    };
  },
  components: {
    LoadingCircle,
    LineChart,
  },
  beforeMount() {
    //Get user's devices
    if (auth.currentUser) {
      const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
      const devQuery = query(devRef, orderByChild("DevName"));
      try {
        onValue(devQuery, (snapshot) => {
          if (snapshot.exists()) {
            this.snaphotExists = true;
            let snapshotData = [];
            snapshot.forEach((child) => {
              snapshotData.push({
                id: child.key,
                description: child.val().Description,
                name: child.val().DevName,
                ssid: child.val().SSID,
                isActive: child.val().isActive,
                lastPing: child.val().LastPing,
                sendSms: child.val().SendSms,
                sendSmsNum: child.val().SendSmsNum,
              });
            });
            this.compareSnaphot(snapshotData);
            this.showPage = true;
          } else {
            this.showPage = true;
          }
        });
      } catch (error) {
        console.error(error);
      }
    }
  },
  beforeUnmount() {
    //Clear time interval and remove listeners
    clearInterval(this.timer);
    this.removeListener(true);
  },

  methods: {
    getLinearChartInterface(LinearChartInterface) {
      this.$options.LinearChartInterface = LinearChartInterface;
    },

    //Check changes in snapshot data
    compareSnaphot: function (newSnapshot) {
      let insertNewData = false;

      if (this.foundDevices == "") {
        insertNewData = true;
      } else if (this.foundDevices.length != newSnapshot.length) {
        insertNewData = true;
      } else {
        for (let i = 0; i < this.foundDevices.length; i++) {
          let tempArray = this.foundDevices[i];
          Object.keys(tempArray).forEach((key) => {
            if (key != "lastPing") {
              if (tempArray[key] != newSnapshot[i][key]) {
                insertNewData = true;
              }
            } else {
              this.foundDevices[i].lastPing = newSnapshot[i][key];
            }
          });

          if (insertNewData) {
            break;
          }
        }
      }

      if (insertNewData) {
        this.foundDevices = newSnapshot;

        this.selectedDevice = null;
        this.showDetails = false;
      }
    },

    //Select user's device
    selectDevice: function (index, clicked) {
      if (this.selectedDevice != index && clicked) {
        if (!this.showDetails) {
          this.showDetails = true;
        }
        document.querySelector("#dev_" + index).classList.add("selected-dev");
        if (this.selectedDevice != null && clicked) {
          document
            .getElementById("dev_" + this.selectedDevice)
            .classList.remove("selected-dev");
        }
        this.selectedDevice = index;
      } else if (this.selectedDevice === index && clicked) {
        if (this.showDetails) {
          this.showDetails = false;
        }
        this.selectedDevice = null;
        document
          .getElementById("dev_" + index)
          .classList.remove("selected-dev");
      }

      if (this.showDetails == true) {
        let pingArray = [];

        let date = new Date();
        let [month, day, year] = [
          date.getMonth(),
          date.getDate(),
          date.getFullYear(),
        ];

        const today = year + "-" + formatNum(month + 1) + "-" + formatNum(day);

        const pingRef = ref(
          rdb,
          "PingTest/" + this.foundDevices[index].id + "/" + today
        );
        const pingQuery = query(pingRef, limitToLast(10));

        this.removeListener(false);
        this.onValueListener =
          "PingTest/" + this.foundDevices[index].id + "/" + today;

        try {
          onValue(pingQuery, (snapshot) => {
            if (!this.checkCurrentDate(month, day, year)) {
              this.selectDevice(index, false);
            }
            this.label = this.createLabel();
            this.values = [];

            if (snapshot.exists()) {
              snapshot.forEach((child) => {
                pingArray.push({ time: child.key, value: child.val() });
              });

              this.label.forEach((element) => {
                let found = pingArray.find((o) => o.time === element);
                if (found) {
                  this.values.push(found.value);
                } else {
                  this.values.push(0);
                }
              });
            } else {
              this.label.forEach(() => {
                this.values.push(0);
              });

              clearInterval(this.timer);
              this.timer = setInterval(() => {
                this.selectDevice(index, false);
              }, 60000);
            }
            this.drawChart();
          });
        } catch (error) {
          console.error(error);
        }
      } else {
        this.removeListener(false);
      }
    },

    //Check if the date has changed when viewing device details
    checkCurrentDate: function (month, day, year) {
      let date = new Date();
      let [newMonth, newDay, newYear] = [
        date.getMonth(),
        date.getDate(),
        date.getFullYear(),
      ];

      if (month === newMonth && day === newDay && year === newYear) return true;
      else return false;
    },

    //Order drawing chart
    drawChart: function () {
      this.$options.LinearChartInterface.clearData();
      this.$options.LinearChartInterface.sendDevPing(this.label, this.values);
    },

    //Create label for chart
    createLabel: function () {
      let date = new Date();
      let [hour, min, sec] = [
        date.getHours(),
        date.getMinutes(),
        date.getSeconds(),
      ];

      let hour_ = hour;
      let min_ = min;
      let sec_ = sec;
      let secToZero = false;

      let label = [];
      if (min_ == 0) {
        if (hour >= 1) {
          hour--;
          min_ = 58;
        } else {
          min_ = 0;
          sec_ = 0;
          secToZero = true;
        }
      } else if (min_ == 1) {
        if (hour_ >= 1) {
          hour_--;
          min_ = 59;
        } else {
          min_ = 0;
          sec_ = 0;
          secToZero = true;
        }
      } else {
        min_ -= 2;
      }

      if (sec_ >= 40 && !secToZero) {
        sec_ = 40;
      } else if (sec_ >= 20) {
        sec_ = 20;
      } else {
        sec_ = 0;
      }

      let startLabel = stringToNum(
        addZeroInTime(hour_, false) +
          addZeroInTime(min_, false) +
          addZeroInTime(sec_, false)
      );
      let nextLabel = this.getNextLabel(startLabel + 20);

      for (let i = 0; i < 7; i++) {
        if (i == 0) {
          label.push(numToTimeString(startLabel));
        } else {
          label.push(numToTimeString(nextLabel));
          nextLabel = this.getNextLabel(nextLabel + 20);
        }

        if (hour == 0 && min < 2 && nextLabel > min * 100 + sec) {
          break;
        }
      }

      return label;
    },

    //Get next item for label
    getNextLabel: function (nextLabel) {
      if (nextLabel % 100 >= 60) {
        nextLabel += 100 - (nextLabel % 100);
        if (nextLabel % 10000 >= 6000) {
          nextLabel += 10000 - (nextLabel % 10000);
        }
      }
      return nextLabel;
    },

    //Check time difference
    isTimeOkay: function (time) {
      let splitTime = time.split(" ");
      let splitDate = splitTime[0].split("-");

      let getDate = Date.parse(
        splitDate[2] +
          "-" +
          splitDate[1] +
          "-" +
          splitDate[0] +
          "T" +
          splitTime[1] +
          ".000+02:00"
      );

      let currentTime = Date.now();

      if (currentTime - getDate <= 1000 * 60 * 10) {
        return true;
      }

      return false;
    },

    //Remove selected device from database
    delDev: function () {
      let confirm = window.confirm("Delete device?");
      if (confirm) {
        const router = useRouter();
        const devRef = ref(
          rdb,
          "Users/" +
            auth.currentUser.uid +
            "/Devices/" +
            this.foundDevices[this.selectedDevice].id
        );
        const pingRef = ref(
          rdb,
          "PingTest/" + this.foundDevices[this.selectedDevice].id
        );

        try {
          remove(devRef);
          remove(pingRef);
          router.replace("/devices");
        } catch (e) {
          console.log(e);
        }
        this.showDetails = false;
      }
    },

    //Remove active RTDB listeners
    removeListener: function (devListener) {
      if (devListener && this.snaphotExists && auth.currentUser) {
        const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
        const devQuery = query(devRef, orderByChild("DevName"));
        off(devQuery);
      }

      if (this.onValueListener != null) {
        const pingRef = ref(rdb, this.onValueListener);
        const pingQuery = query(pingRef, limitToLast(10));
        off(pingQuery);
        this.onValueListener = null;
      }
    },
  },
};
</script>

<style scoped>
.split-dev-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 20px;
  height: 90%;
}

.dev-list {
  text-align: start;
  margin-left: 6px;
  display: flex;
  flex-wrap: wrap;
}

.dev-item {
  border: 2px solid var(--dark-gray);
  border-radius: 10px;
  box-shadow: var(--box-shadow);
  width: 200px;
  margin: 10px;
  padding: 5px;
  user-select: none;
  transition: all 0.5s;
  display: grid;
  grid-template-columns: 3fr 1fr;
}

.dev-item:hover {
  background-color: var(--dark-violet);
  color: var(--white);
  transition: all 0.5s;
  cursor: pointer;
}

.dev-name {
  font-weight: bold;
}

.dev-item-detail > p {
  padding: 0;
  margin: 0;
}

.dev-item-warning {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bolder;
}

.dev-item.dev-warning {
  border-color: var(--warning-red);
  animation: warning-blink 2s ease-in-out infinite;
}

.selected-dev {
  background-color: var(--dark-violet);
  color: var(--white);
}

.split-dev-details {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-gap: 10px;
  width: 100%;
}

.split-dev-details > .left {
  text-align: start;
}

.split-dev-details > .right {
  text-align: center;
  align-items: center;
}

.dev-details-empty {
  margin-left: 6px;
}

.dev-details-filled {
  margin-left: 6px;
}

.details-field {
  font-weight: bolder;
}

.dev-chart > div > canvas {
  height: 200px;
}

.del-dev {
  font-weight: bolder;
  color: var(--warning-red);
  padding: 0.5rem;
  border: solid var(--warning-red) 1px;
  border-radius: 20px;
  width: fit-content;
  margin-left: auto;
  margin-right: 0;
  user-select: none;
  transition: all 0.5s;
}

.del-dev:hover {
  color: var(--white);
  background-color: var(--warning-red);
  transition: all 0.5s;
  cursor: pointer;
}

.time-not-ok {
  color: var(--warning-red);
  font-weight: bold;
}
</style>