<template>
  <div class="content-header">
    <h2>Ping result</h2>
  </div>

  <div v-if="!showPage" class="loadingCircle"><LoadingCircle /></div>
  <div v-else-if="foundNetworks == ''">No data found</div>

  <div v-else class="split-ping-content">
    <div class="split-left">
      <h4 class="split-content-header">Result filter</h4>
      <select
        name="ssidSelect"
        id="ssidSelect"
        @change="chooseNetwork()"
        class="form-select animate-slow"
        style="margin-top: 20px"
      >
        <option value="" selected disabled hidden>Select network</option>
        <option v-for="item in foundNetworks" :key="item" :value="item">
          {{ item }}
        </option>
      </select>

      <div v-if="selectedNetwork" class="animate-slow">
        <h5 style="margin-top: 20px">Date selection</h5>

        <div class="date-picker-container">
          <div class="grid-item grid-item-left">Date</div>
          <div class="grid-item grid-item-right">
            <CalendarComponent @interface="getCalendarInterface" />
          </div>

          <div class="grid-item grid-item-left">Starting time</div>
          <div class="grid-item grid-item-right">
            <input
              type="time"
              id="timeStart"
              name="timeStart"
              class="form-control"
            />
          </div>

          <div class="grid-item grid-item-left">Ending time</div>
          <div class="grid-item grid-item-right">
            <input
              type="time"
              id="timeEnd"
              name="timeEnd"
              class="form-control"
            />
          </div>
        </div>
        <button v-on:click="sendData" class="btn btn-lg btn-outline-main">
          Search
        </button>
      </div>
    </div>

    <div class="split-right">
      <h4 class="split-content-header">Ping chart</h4>
      <div v-if="!showChart" class="animate-slow">
        Fill filter options to show chart
      </div>
      <div v-if="showValidationError" class="ping-error-list animate-slow">
        <p v-for="item in errorList" :key="item">{{ item }}</p>
      </div>
      <div v-show="showChart" class="ping-chart animate-slow">
        <LineChart @interface="getLinearChartInterface"></LineChart>
      </div>
    </div>
  </div>
</template>

<script>
import { auth, rdb } from "@/firebase/index.js";
import {
  ref,
  get,
  query,
  orderByChild,
  equalTo,
  orderByKey,
  startAt,
  endAt,
  off,
} from "firebase/database";
import LoadingCircle from "@/components/LoadingCircle.vue";
import LineChart from "@/components/LineChartComponent.vue";
import CalendarComponent from "@/components/CalendarComponent.vue";
import { stringToNum } from "@/utils/index.js";

export default {
  name: "PingComponent",
  LinearChartInterface: {
    sendPingData: () => {},
    clearData: () => {},
  },
  CalendarInterface: {
    getDate: () => {},
  },
  data() {
    return {
      showPage: true,
      showChart: false,
      showValidationError: false,
      selectedNetwork: false,
      dateValidateFail: false,
      snaphotExists: false,
      date: null,
      startTime: null,
      endTime: null,
      foundNetworks: [],
      foundDevices: [],
      foundPings: [],
      errorList: [],
    };
  },
  components: {
    LoadingCircle,
    LineChart,
    CalendarComponent,
  },
  beforeMount() {
    //Get SSIDs from user's devices
    if (auth.currentUser) {
      const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
      const devQuery = query(devRef, orderByChild("SSID"));
      try {
        get(devQuery).then((snapshot) => {
          if (snapshot.exists()) {
            this.foundDevices = [];
            this.snaphotExists = true;
            snapshot.forEach((child) => {
              if (this.foundNetworks.indexOf(child.val().SSID) == -1) {
                this.foundNetworks.push(child.val().SSID);
              }
            });
            this.showPage = true;
          } else {
            this.showPage = true;
          }
        });
      } catch (error) {
        console.error(error);
      }

      //Redraw chart after window resize
      addEventListener("resize", () => {
        if (this.showChart) {
          this.$options.LinearChartInterface.redraw();
        }
      });
    }
  },
  beforeUnmount() {
    //Clear fields and remove listeners
    if (this.selectedNetwork) {
      document.querySelector("#timeStart").value = "";
      document.querySelector("#timeEnd").value = "";
    }
    this.removeListener();
  },

  methods: {
    //Parent-child communication
    getLinearChartInterface(LinearChartInterface) {
      this.$options.LinearChartInterface = LinearChartInterface;
    },
    getCalendarInterface(CalendarInterface) {
      this.$options.CalendarInterface = CalendarInterface;
    },
    sendData() {
      this.searchSingleDay();
    },

    //Get devices in choosen network
    chooseNetwork: function () {
      this.selectedNetwork = true;
      this.foundDevices = [];

      const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
      const devQuery = query(
        devRef,
        orderByChild("SSID"),
        equalTo(document.querySelector("#ssidSelect").value)
      );
      get(devQuery)
        .then((snapshot) => {
          snapshot.forEach((child) => {
            this.foundDevices.push({
              ID: child.key,
              Name: child.val().DevName,
            });
          });
        })
        .catch((error) => {
          console.error(error);
        });
    },

    //Search ping result for devices
    searchSingleDay: function () {
      this.clearValidation();
      this.$options.LinearChartInterface.clearData();

      this.showChart = true;
      this.showValidationError = false;
      this.foundPings = [];

      this.date = this.$options.CalendarInterface.getDate();
      this.startTime = document.querySelector("#timeStart").value + ":00";
      this.endTime = document.querySelector("#timeEnd").value + ":00";

      if (this.validateData()) {
        let devicesNum = this.foundDevices.length;
        for (let i = 0; i < devicesNum; i++) {
          this.getPingSingleDay(
            this.foundDevices[i].ID,
            this.foundDevices[i].Name,
            this.date,
            this.startTime,
            this.endTime,
            i,
            devicesNum
          );
        }
        document
          .getElementsByTagName("canvas")[0]
          .setAttribute("style", "display:block;");
      } else {
        this.showValidationError = true;
        document
          .getElementsByTagName("canvas")[0]
          .setAttribute("style", "display:none;");
      }
    },

    //Firebase communication
    getPingSingleDay: function (
      deviceId,
      deviceName,
      dateFilter,
      startTime,
      endTime,
      i,
      devicesNum
    ) {
      const pingRef = ref(rdb, "PingTest/" + deviceId + "/" + dateFilter);
      const pingQuery = query(
        pingRef,
        orderByKey(),
        startAt(startTime),
        endAt(endTime)
      );

      get(pingQuery)
        .then((snapshot) => {
          let obj = {};
          let pings = [];

          snapshot.forEach((child) => {
            if (child.key.slice(6) == "00") {
              pings.push({ time: child.key, value: child.val() });
            }
          });

          obj = { deviceId: deviceId, deviceName: deviceName, ping: pings };

          if (i == devicesNum - 1) {
            this.$options.LinearChartInterface.sendPingData(
              obj,
              startTime,
              endTime,
              true
            );
          } else {
            this.$options.LinearChartInterface.sendPingData(
              obj,
              startTime,
              endTime,
              false
            );
          }
        })
        .catch((error) => {
          console.error(error);
        });
    },

    //Date vaildation
    validateData: function () {
      let validationFailed = false;
      this.errorList = [];

      if (!this.selectedNetwork) {
        validationFailed = true;
        this.errorList.push("No network selected");
        document.querySelector("#ssidSelect").classList.add("validateFail");
      }

      if (this.date == null) {
        validationFailed = true;
        this.errorList.push("Select date from calendar");
        document.querySelector("#callendar").classList.add("validateFail");
      }

      if (this.startTime == ":00" || this.endTime == ":00") {
        validationFailed = true;
        this.errorList.push("Select filter time");
        document.querySelector("#timeStart").classList.add("validateFail");
        document.querySelector("#timeEnd").classList.add("validateFail");
      } else {
        let startTime = stringToNum(this.startTime);
        let endTime = stringToNum(this.endTime);

        if (endTime - startTime < 1000) {
          validationFailed = true;
          this.errorList.push("Selected time range must be minimum 10 minutes");
          document.querySelector("#timeStart").classList.add("validateFail");
          document.querySelector("#timeEnd").classList.add("validateFail");
        }
      }

      if (validationFailed) {
        return false;
      }
      return true;
    },
    clearValidation: function () {
      document.querySelector("#ssidSelect").classList.remove("validateFail");
      document.querySelector("#callendar").classList.remove("validateFail");
      document.querySelector("#timeStart").classList.remove("validateFail");
      document.querySelector("#timeEnd").classList.remove("validateFail");
    },

    //Remove active RTDB listeners
    removeListener: function () {
      if (auth.currentUser && this.snaphotExists) {
        const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
        const devQuery = query(
          devRef,
          orderByChild("SSID"),
          equalTo(document.querySelector("#ssidSelect").value)
        );
        off(devQuery);
      }

      this.snaphotExists = false;
    },
  },
};
</script>

<style scoped>
.split-ping-content {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-gap: 20px;
  height: 90%;
}

.date-picker-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.date-picker-container > .grid-item {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  padding: 10px;
}

.ping-error-list {
  color: var(--warning-red);
  font-weight: bold;
}

.validateFail {
  border: solid 1px var(--warning-red);
  transition: all 0.5s;
}
</style>