function addZeroInTime(value, endTime) {
    let newValue;
    if (value < 10) {
        newValue = "0" + value;
    } else {
        newValue = value;
    }

    if (!endTime) {
        return newValue + ":";
    } else {
        return newValue;
    }
}

function formatNum(num) {
    if (num < 10) {
        let returnNum = "0";
        returnNum += String(num);
        return returnNum;
    } else {
        return String(num);
    }
}

function numToTimeString(number) {
    if (number == 0) {
        return "00:00:00";
    } else if (number < 100) {
        return "00:00:" + String(number).slice(0, 2);
    } else if (number < 1000) {
        return (
            "00:0" + String(number).slice(0, 1) + ":" + String(number).slice(1, 3)
        );
    } else if (number < 10000) {
        return (
            "00:" + String(number).slice(0, 2) + ":" + String(number).slice(2, 4)
        );
    } else if (number < 100000) {
        return (
            "0" +
            String(number).slice(0, 1) +
            ":" +
            String(number).slice(1, 3) +
            ":" +
            String(number).slice(3, 5)
        );
    } else {
        return (
            String(number).slice(0, 2) +
            ":" +
            String(number).slice(2, 4) +
            ":" +
            String(number).slice(4)
        );
    }
}

function stringToNum(string) {
    string = string.replaceAll(":", "");
    return Number(string);
}



export { addZeroInTime, formatNum, numToTimeString, stringToNum };