import { createStore } from 'vuex'

export default createStore({
  state: {
    loggedUser: false
  },
  getters: {
    loggedUser: state => state.loggedUser,
  },
  mutations: {
    logUser(state) {
      state.loggedUser = true
    },
    logoutUser(state) {
      state.loggedUser = false
    }
  },
  actions: {
  },
  modules: {
  }
})