<template>
  <Datepicker
    v-model="date"
    id="callendar"
    :enableTimePicker="false"
    :max-date="new Date()"
    format="dd/MM/yyyy"
    type="date"
    modelType="yyyy-MM-dd"
    popper-class="custom-theme"
  ></Datepicker>
</template>

<script>
import Datepicker from "@vuepic/vue-datepicker";
import "@vuepic/vue-datepicker/dist/main.css";

export default {
  name: "CalendarComponent",
  components: { Datepicker },
  emits: ["interface"],
  data() {
    return {
      date: null,
    };
  },
  mounted() {
    this.emitInterface();
  },
  methods: {
    emitInterface: function () {
      this.$emit("interface", {
        getDate: () => {
          return this.date;
        },
      });
    },
  },
};
</script>

<style scoped>
#callendar {
  border-radius: 0.375rem;
}
</style>