<template>
  <div class="content-header">
    <h2>Raport list</h2>
  </div>

  <div v-if="!showPage" class="loadingCircle"><LoadingCircle /></div>
  <div v-else-if="foundNetworks == ''">No data found</div>

  <div v-else class="split-raport-content">
    <div class="split-left">
      <h4 class="split-content-header">Raport filter</h4>
      <div class="raport-filter-container animate-slow">
        <div class="grid-item grid-item-left">Network:</div>
        <div class="grid-item grid-item-right">
          <select
            name="ssidSelect"
            id="ssidSelect"
            class="form-select"
            @change="getRaports()"
          >
            <option value="" selected disabled hidden>Choose network</option>
            <option v-for="item in foundNetworks" :key="item" :value="item">
              {{ item }}
            </option>
          </select>
        </div>

        <div
          v-if="selectedNetwork"
          class="grid-item grid-item-left animate-slow"
        >
          Device:
        </div>
        <div
          v-if="selectedNetwork"
          class="grid-item grid-item-right animate-slow"
        >
          <select
            name="devSelect"
            id="devSelect"
            class="form-select"
            @change="selectDevice()"
          >
            <option value="" selected disabled hidden>Select device</option>
            <option v-for="(item, index) in raports" :key="item" :value="index">
              {{ item.device }}
            </option>
          </select>
        </div>
      </div>
    </div>

    <div class="split-right">
      <h4 class="split-content-header">Found events</h4>
      <div v-if="!raportsSearched" class="animate-slow">
        Select network and device to show raports
      </div>
      <div v-else-if="hasNoRaports" class="animate-slow">No raports found</div>
      <div v-else class="raport-list">
        <div
          class="raport-item animate-slow"
          v-for="(item, index) in this.renderRaports[this.selectedDev].raport"
          :key="item"
        >
          <h5>{{ index }}</h5>
          <div class="split-raport-details">
            <div class="left">Error Started:</div>
            <div class="right">
              {{ this.getCreationTime(item.ErrorStart, 0) }}
              <br />
              {{ this.getCreationTime(item.ErrorStart, 1) }}
            </div>

            <div class="left">Error time:</div>
            <div class="right">
              {{ this.countTime(item.ErrorStart, item.ErrorEnd) }}
            </div>
          </div>
          <div v-if="item.ErrorType == 0">Network error</div>
          <div v-if="item.ErrorType == 1">Local error</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { auth, rdb } from "@/firebase/index.js";
import {
  ref,
  query,
  orderByChild,
  equalTo,
  onValue,
  off,
} from "firebase/database";
import LoadingCircle from "@/components/LoadingCircle.vue";

export default {
  name: "RaportComponent",
  data() {
    return {
      foundNetworks: [],
      raports: [],
      renderRaports: [],
      snaphotExists: false,
      showPage: false,
      raportsSearched: false,
      selectedNetwork: false,
      hasNoRaports: false,
      selectedDev: null,
    };
  },
  components: {
    LoadingCircle,
  },
  beforeMount() {
    //Get SSIDs from user's devices
    if (auth.currentUser) {
      const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
      const devQuery = query(devRef, orderByChild("SSID"));

      try {
        onValue(devQuery, (snapshot) => {
          if (snapshot.exists()) {
            this.snaphotExists = true;
            this.foundDevices = [];
            snapshot.forEach((child) => {
              if (this.foundNetworks.indexOf(child.val().SSID) == -1) {
                this.foundNetworks.push(child.val().SSID);
              }
            });
            this.showPage = true;
          } else {
            this.showPage = true;
          }
        });
      } catch (error) {
        console.error(error);
      }
    }
  },
  beforeUnmount() {
    //Remove listeners
    this.removeListener();
  },

  methods: {
    //Get all raports for devices
    getRaports: function () {
      this.raportsSearched = false;
      this.hasNoRaports = false;

      if (auth.currentUser) {
        this.raports = [];
        const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
        const devQuery = query(
          devRef,
          orderByChild("SSID"),
          equalTo(document.querySelector("#ssidSelect").value)
        );

        try {
          onValue(devQuery, (snapshot) => {
            if (snapshot.exists()) {
              this.raports = [];
              snapshot.forEach((child) => {
                if (child.hasChild("Raports")) {
                  this.raports.push({
                    device: child.val().DevName,
                    raport: child.child("Raports").val(),
                  });
                } else {
                  this.raports.push({
                    device: child.val().DevName,
                    raport: {},
                  });
                }
              });
            }

            if (this.renderRaports.length != this.raports.length) {
              this.renderRaports = this.raports;
            }
          });
        } catch (error) {
          console.error(error);
        }
      }

      if (this.selectedNetwork) {
        document.querySelector("#devSelect").selectedIndex = 0;
      }
      this.selectedNetwork = true;
    },

    //Select device
    selectDevice: function () {
      this.selectedDev = document.querySelector("#devSelect").value;
      this.raportsSearched = true;

      if (Object.keys(this.raports[this.selectedDev].raport).length === 0) {
        this.hasNoRaports = true;
      } else {
        this.hasNoRaports = false;
      }
    },

    //Count and format error time
    countTime: function (errStart, errEnd) {
      errStart = errStart.split(" ");
      errEnd = errEnd.split(" ");

      let errStartDay = errStart[0].split("-");
      let errEndDay = errEnd[0].split("-");

      let ErrStartDate = Date.parse(
        errStartDay[2] +
          "-" +
          errStartDay[1] +
          "-" +
          errStartDay[0] +
          "T" +
          errStart[1] +
          ".000+00:00"
      );
      let ErrEndDate = Date.parse(
        errEndDay[2] +
          "-" +
          errEndDay[1] +
          "-" +
          errEndDay[0] +
          "T" +
          errEnd[1] +
          ".000+00:00"
      );

      return this.getTimeDiff(ErrStartDate, ErrEndDate);
    },
    getTimeDiff: function (ErrStartDate, ErrEndDate) {
      if (Math.floor((ErrEndDate - ErrStartDate) / (1000 * 60 * 60 * 24)) > 0) {
        return (
          "> " +
          Math.floor((ErrEndDate - ErrStartDate) / (1000 * 60 * 60 * 24)) +
          " d"
        );
      } else if (
        Math.floor((ErrEndDate - ErrStartDate) / (1000 * 60 * 60)) > 0
      ) {
        return (
          "> " +
          Math.floor((ErrEndDate - ErrStartDate) / (1000 * 60 * 60)) +
          " h"
        );
      } else if (Math.floor((ErrEndDate - ErrStartDate) / (1000 * 60)) > 0) {
        return (
          "> " + Math.floor((ErrEndDate - ErrStartDate) / (1000 * 60)) + " min"
        );
      } else {
        return "> " + Math.floor((ErrEndDate - ErrStartDate) / 1000) + " sec";
      }
    },

    //Format error start info
    getCreationTime: function (time, index) {
      time = time.split(" ");
      return time[index];
    },

    //Remove active RTDB listeners
    removeListener: function () {
      if (auth.currentUser && this.snaphotExists) {
        const devRef = ref(rdb, "Users/" + auth.currentUser.uid + "/Devices");
        const devQuery = query(
          devRef,
          orderByChild("SSID"),
          equalTo(document.querySelector("#ssidSelect").value)
        );
        off(devQuery);
      }

      this.snaphotExists = false;
    },
  },
};
</script>

<style scoped>
.split-raport-content {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 20px;
  height: 90%;
}

.raport-filter-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.raport-filter-container > .grid-item {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  padding: 10px;
}

.raport-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 5px;
}

.raport-item {
  display: flex;
  flex-direction: column;
  padding: 5px;
  margin: 10px;
  border: 2px solid #ddd;
  border-radius: 20px;
  box-shadow: var(--box-shadow);
  width: fit-content;
}

.split-raport-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.split-raport-details > .left {
  text-align: start;
}

.split-raport-details > .right {
  text-align: end;
}
</style>