<template>
  <div class="login-box">
    <div class="login-fields">
      <h2 class="m-0 animate-slower">{{ appName }}</h2>
      <h3 class="animate-slower">{{ type }}</h3>
      <form @submit.prevent="Login">
        <div class="form-floating animate-slower" style="color: gray">
          <input
            type="email"
            class="form-control"
            id="emailInput"
            placeholder="name@example.com"
            v-model="email"
            required
          />
          <label for="emailInput">Email address</label>
        </div>
        <div class="form-floating mt-3 animate-slower" style="color: gray">
          <input
            type="password"
            class="form-control"
            id="passInput"
            placeholder="name@example.com"
            v-model="password"
            required
          />
          <label for="passInput">Password</label>
        </div>
        <div class="login-buttons animate-slower">
          <input type="submit" value="Login" class="btn btn-outline-main" />
        </div>
      </form>
      <div class="reg-log-text animate-slower" @click="Register">
        Need an account? <br />
        <span class="reg-log-link">Click here</span>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { auth } from "@/firebase/index.js";
import {
  signInWithEmailAndPassword,
  setPersistence,
  browserSessionPersistence,
} from "firebase/auth";
import { useRouter } from "vue-router";
import store from "@/store/index.js";

export default {
  name: "LoginComponent",
  props: {
    appName: String,
    type: String,
  },
  setup() {
    const email = ref("");
    const password = ref("");
    const router = useRouter();

    //Prepare Login function
    const Login = () => {
      setPersistence(auth, browserSessionPersistence).then(async () => {
        try {
          await signInWithEmailAndPassword(auth, email.value, password.value);
          store.commit("logUser");
          router.replace("/devices");
        } catch (e) {
          alert("Incorrect email or password");
          email.value = "";
          password.value = "";
        }
      });
    };

    //Prepare redirection to Register form
    const Register = () => {
      router.replace("/register");
    };

    return {
      Login,
      Register,
      email,
      password,
    };
  },
};
</script>

<style scoped>
.login-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
}

.login-fields {
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  background: var(--light-gray);
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1.5rem;
  box-shadow: var(--box-shadow);
}

.login-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  margin-top: 1.5rem;
}

.reg-log-text {
  justify-content: center;
  color: var(--dark-violet);
  font-weight: bolder;
}

.reg-log-link:hover {
  cursor: pointer;
  text-decoration: underline;
}
</style>