<template>
  <div v-if="draw" class="chart-menu">
    <div
      v-for="(item, index) in charts"
      :key="item"
      @click="selectChart({ index })"
      :id="'chart_' + index"
      class="chart-menu-item"
    >
      {{ item.deviceName }}
    </div>
  </div>

  <Line
    :chartData="chartData"
    :chartOptions="chartOptions"
    :chartId="chartId"
    :width="width"
    :height="height"
  />
</template>

<script>
import { Line } from "vue-chartjs";
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  Filler,
} from "chart.js";
import { formatNum } from "@/utils/index.js";

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  CategoryScale,
  PointElement,
  Filler
);

export default {
  name: "LineChart",
  components: { Line },
  emits: ["interface"],
  props: {
    chartId: {
      type: String,
      default: "line-chart",
    },
    width: {
      type: Number,
      default: 3,
    },
    height: {
      type: Number,
      default: 1,
    },
  },
  mounted() {
    this.emitInterface();
  },
  updated() {
    if (document.querySelector("#chart_" + this.chartNum)) {
      document
        .getElementById("chart_" + this.chartNum)
        .classList.add("selected-chart");
    }
  },
  data() {
    return {
      labels: [],
      data: [],
      charts: [],
      chartNum: 0,
      nextTime: 0,
      startTime: "",
      endTime: "",
      previousTime: 0,
      draw: false,
      chartData: {
        labels: [],
        datasets: [],
      },
      chartOptions: {
        scales: {
          y: {
            suggestedMin: 0,
            suggestedMax: 150,
            ticks: {
              callback: function (value) {
                return value + " ms";
              },
            },
          },
        },
        responsive: true,
        plugins: {
          legend: {
            display: false,
          },
        },
      },
    };
  },
  methods: {
    emitInterface: function () {
      this.$emit("interface", {
        sendDevPing: (label, value) => {
          this.sendDevPing(label, value);
        },
        sendPingData: (obj, startTime, endTime, draw) => {
          this.sendPingData(obj, startTime, endTime, draw);
        },
        redraw: () => {
          this.redraw();
        },
        clearData: () => {
          this.chartData.labels = [];
          this.chartData.datasets = [];
          this.charts = [];
          this.labels = [];
          this.data = [];
          this.draw = false;
        },
      });
    },

    //Send data do chart in Devices View
    sendDevPing: function (label, value) {
      this.chartData.labels = label;
      this.chartData.datasets.push({
        label: "Last Ping",
        borderColor: "var(--dark-violet)",
        backgroundColor: "rgba(52,34,68,0.3)",
        pointBackgroundColor: "black",
        borderWidth: 1,
        radius: 0,
        fill: true,
        pointBorderColor: "black",
        data: value,
      });
    },

    //Send data do chart in Ping History
    sendPingData: function (obj, startTime, endTime, draw) {
      this.charts.push(obj);
      this.startTime = startTime;
      this.endTime = endTime;
      this.draw = draw;

      if (draw) {
        this.drawPingChart(this.charts, startTime, endTime);
      }
    },

    //Format data for Ping History and draw chart
    drawPingChart: function (charts, startTime, endTime) {
      this.data = [];
      this.chartData.datasets = [];

      if (this.labels.length == 0) {
        this.fillLabels(startTime, endTime);
        this.chartData.labels = this.labels;
      }

      for (let i = 0; i < this.labels.length; i++) {
        this.data.push(0);
      }

      if (!charts[this.chartNum].ping.length == 0) {
        for (let x = 0; x < this.labels.length; x++) {
          for (let y = 0; y < charts[this.chartNum].ping.length; y++) {
            if (this.labels[x] == charts[this.chartNum].ping[y].time) {
              this.data[x] = charts[this.chartNum].ping[y].value;
              break;
            }
          }
        }
      }

      this.chartData.datasets.push({
        label: charts[this.chartNum].deviceId,
        borderColor: "var(--dark-violet)",
        backgroundColor: "rgba(52,34,68,0.3)",
        pointBackgroundColor: "var(--white)",
        borderWidth: 1,
        radius: 0,
        fill: true,
        pointBorderColor: "var(--white)",
        data: this.data,
      });
    },

    //Fill labels with data
    fillLabels: function (startTime, endTime) {
      let startTimeArray = startTime.split(":");
      let endTimeArray = endTime.split(":");

      let startTimeDate = new Date();
      startTimeDate.setHours(startTimeArray[0]);
      startTimeDate.setMinutes(startTimeArray[1]);
      startTimeDate.setSeconds("0");

      let endTimeDate = new Date();
      endTimeDate.setHours(endTimeArray[0]);
      endTimeDate.setMinutes(endTimeArray[1]);
      endTimeDate.setSeconds("0");

      while (startTimeDate.getTime() != endTimeDate.getTime()) {
        this.labels.push(this.timeToLabel(startTimeDate));

        startTimeDate.setMinutes(startTimeDate.getMinutes() + 1);
      }
      this.labels.push(this.timeToLabel(startTimeDate));
    },

    //Change time to label format
    timeToLabel: function (time) {
      let label =
        this.formatNum(time.getHours()) +
        ":" +
        this.formatNum(time.getMinutes()) +
        ":" +
        this.formatNum(time.getSeconds());

      return label;
    },

    //Number -> String
    formatNum: function (num) {
      return formatNum(num);
    },

    //Select Device to show Ping History
    selectChart: function (value) {
      document
        .getElementById("chart_" + this.chartNum)
        .classList.remove("selected-chart");
      this.chartNum = value.index;
      document
        .getElementById("chart_" + this.chartNum)
        .classList.add("selected-chart");
      this.drawPingChart(this.charts, this.startTime, this.endTime);
    },

    //Redraw chart
    redraw: function () {
      document.querySelector("#line-chart").style.width = "100%";
    },
  },
};
</script>

<style scoped>
.chart-menu {
  display: flex;
  flex-direction: row;
  margin: 20px 0 20px 20px;
  user-select: none;
}

.chart-menu-item {
  margin-right: 0.25rem;
  border: 1px solid var(--black);
  padding: 0.25rem;
  cursor: pointer;
  transition: all 0.5s;
  font-weight: bold;
  background-color: var(--white);
}

.chart-menu-item:hover {
  background-color: var(--dark-violet);
  color: var(--white);
  transition: all 0.5s;
}

.chart-menu-item:first-of-type {
  border-radius: 10px 0 0 10px;
}

.chart-menu-item:last-of-type {
  border-radius: 0 10px 10px 0;
}

.chart-menu-item:only-of-type {
  border-radius: 10px 10px 10px 10px;
}

.selected-chart {
  background-color: var(--dark-violet);
  color: var(--white);
}

.ping-chart > div > canvas {
  width: 100%;
  height: 200px;
}
</style>